<template>
  <div id="pay_box">
    <div v-if="userName"
         class="font75">{{ userName }}邀请你加入</div>
    <div class="c_8a">{{ $route.query.deptName }}</div>
    <div class="please">请使用微信扫码注册</div>
    <img :src="code"
         style="width:100%;">
  </div>
</template>
<script>
import { GetSystemDeptUserXcxCode } from '@/api/api_depart';
export default {
    data() {
        return {
            userName: null,
            currentInfo: null,
            status: '0',
            statusInfo: null,
            code: null,
            uploader: null,
            photos: []
        };
    },
    created() {
        this.$store.commit('setHeader', '邀请二维码');
        this.userName = JSON.parse(localStorage.getItem('current_user')).realName || JSON.parse(localStorage.getItem('current_user')).nickName;
        this.getCode();
    },
    methods: {
        getCode() {
            GetSystemDeptUserXcxCode({ deptId: this.$route.query.deptId }).done((res) => {
                this.code = 'data:image/png;base64,' + res.data;
            });
        }
    }
};
</script>
<style scoped>
#pay_box {
    font-size: 0.8rem;
    margin: 20px;
    background: #fff;
    padding: 16px 0;
    color: #000;
    border-radius: 8px;
}
.font75 {
    font-size: 0.75rem;
}
.c_8a {
    color: #8a8a8a;
}
.please {
    font-weight: bold;
    margin: 50px 0 20px;
    font-size: 1rem;
}
</style>



